import { List } from "immutable";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { DEFAULT_DP, DEFAULT_MAXSET, DEFAULT_NAME_A, DEFAULT_NAME_B, DEFAULT_STB, Event, useStore } from "./store";

type Scores = {
	readonly gA: number;
	readonly gB: number;
}

const Scores = (gA: number, gB: number): Scores => Object.freeze({ gA, gB });

export const App = () => {

	const state = useStore();
	const serv = state.serv();
	const { pA, pB } = state.points.last()!;
	const { gA, gB } = state.games.last()!;

	const [showDropdown, setShowDropdown] = useState(false);

	const onMenuClick = useCallback(() => setShowDropdown(show => !show), [setShowDropdown]);
	const menuButton = useRef<HTMLButtonElement>(null);

	useEffect(() => {
		const onClick = (e: MouseEvent) => {
			if (e.target !== menuButton.current) {
				setShowDropdown(false);
			}
		}

		window.addEventListener("click", onClick);
		return () => window.removeEventListener("click", onClick);
	}, []);

	const label = useMemo(() => {
		if (state.finished) {
			return "Koniec";
		}

		const { sA, sB } = state;

		let label = "";
		if (pA === 0 && pB === 0) {
			if (gA === 0 && gB === 0) {
				if (sA > 0 || sB > 0) {
					label = "Set";
					if (!state.fault) {
						label += ", zmiana stron";
					}

					if (state.supertiebreak) {
						label += ", super tiebreak";
					}
				} else {
					label = "Początek";
				}
			} else {
				const winningPoint = state.points.get(-2)!;
				label = "Gem ";
				if (winningPoint.pA > winningPoint.pB) {
					label += serv ? "serwis " : "return ";
					label += `${gA}-${gB}`;
				} else {
					label += serv ? "return " : "serwis ";
					label += `${gB}-${gA}`;
				}

				if (state.tiebreak) {
					label += ", tiebreak";
				}

				if (sA === state.maxset - 1 && sB === state.maxset - 1 && (gA + gB) % 4 === 0 && !state.fault) {
					label += ", zmiana stron";
				}
			}
		} else {
			if (pA === pB) {
				if (state.tiebreak || state.supertiebreak) {
					label = `${pA}-${pB}`;
					if (state.supertiebreak && (pA + pB) % 8 === 0 && !state.fault) {
						label += ", zmiana stron";
					}
				} else if (pA >= 3) {
					const dc = pA - 2;
					if (state.dp === 1) {
						label = "3-3";
					} else if (state.dp > 0 && dc >= state.dp) {
						label = "DECPOINT";
					} else {
						label = "Równowaga";
						if (state.dp !== 0) {
							label += ` ${dc}`;
						}
					}
				} else {
					return `${pA}-${pB}`;
				}
			} else {
				if (state.tiebreak || state.supertiebreak) {
					if (pA > pB) {
						label = `${pA}-${pB}`;
					} else {
						label = `${pB}-${pA}`;
					}

					if (state.supertiebreak && (pA + pB) % 8 === 0 && !state.fault) {
						label += ", zmiana stron";
					}
				} else if (pA >= 3 && pB >= 3) {
					if (pA > pB && !serv || pB > pA && serv) {
						label = "Przewaga serwis";
					} else {
						label = "Przewaga return";
					}
				} else {
					if (pA > pB) {
						label = `${pA}-${pB}`;
						if (serv) {
							label += " return";
						} else {
							label += " serwis";
						}
					} else {
						label = `${pB}-${pA}`;
						if (serv) {
							label += " serwis";
						} else {
							label += " return";
						}
					}
				}
			}
		}

		if (state.fault) {
			label += ", II serwis";
		}

		return label;
	}, [state]);

	const scores = useMemo(() => {
		let scores = List<Scores>();
		for (const { gA, gB } of state.sets) {
			scores = scores.push(Scores(gA, gB));
		}
		if (!state.finished) {
			const { gA, gB } = state.games.last()!;
			scores = scores.push(Scores(gA, gB));
		}
		return scores;
	}, [state]);

	const points = useMemo<readonly [string, string]>(() => {
		if (state.finished) {
			return ["", ""];
		} else if (!state.tiebreak && !state.supertiebreak && pA >= 3 && pB >= 3) {
			if (pA === pB) {
				return ["D", "D"];
			} else if (pA > pB) {
				return ["A", ""];
			} else {
				return ["", "A"];
			}
		} else {
			return [pA.toString(), pB.toString()];
		}
	}, [state]);

	return (<>
		<div id="top">
			<div id="label">{label}</div>
			<div id="menu">
				<button ref={menuButton} onClick={onMenuClick}>...</button>
				<ul className={`dropdown ${showDropdown ? "show" : ""}`}>
					<li role="button" onClick={() => state.new(DEFAULT_NAME_A, DEFAULT_NAME_B, DEFAULT_MAXSET, DEFAULT_STB, DEFAULT_DP)}>Nowy mecz</li>
					<li role="button" onClick={() => state.back()}>Cofnij</li>
				</ul>
			</div>
		</div>
		<div id="event-buttons">
			<button className="flex-1" onClick={() => state.event(Event.BigShotA)}>Big Shot</button>
			<button className="flex-1" onClick={() => state.event(Event.UnforcedErrorA)}>Unforced Error</button>
			<div className="flex-1" id="event-buttons-serve">
				<button className="flex-1" onClick={() => state.event(Event.BigServe)}>Big Serve</button>
				<button className="flex-1" onClick={() => state.event(Event.Fault)}>Fault</button>
			</div>
			<button className="flex-1" onClick={() => state.event(Event.UnforcedErrorB)}>Unforced Error</button>
			<button className="flex-1" onClick={() => state.event(Event.BigShotB)}>Big Shot</button>
		</div>
		<div id="names">
			<div className="name flex-2">{state.nameA}</div>
			<div className="flex-1"></div>
			<div className="name flex-2">{state.nameB}</div>
		</div>
		<table id="scores">
			<tbody>
				<tr>
					<td className="flex-32px score">{!serv ? (state.fault ? "F" : "O") : ""}</td>
					<td className="flex-1 name">{state.nameA}</td>
					<td className="flex-32px score">{scores.get(0)?.gA ?? ""}</td>
					<td className="flex-32px score">{scores.get(1)?.gA ?? ""}</td>
					<td className="flex-32px score">{scores.get(2)?.gA ?? ""}</td>
					<td className="flex-32px score">{scores.get(3)?.gA ?? ""}</td>
					<td className="flex-32px score">{scores.get(4)?.gA ?? ""}</td>
					<td className="flex-32px score">{points[0]}</td>
				</tr>
				<tr>
					<td className="flex-32px score">{serv ? (state.fault ? "F" : "O") : ""}</td>
					<td className="flex-1 name">{state.nameB}</td>
					<td className="flex-32px score">{scores.get(0)?.gB ?? ""}</td>
					<td className="flex-32px score">{scores.get(1)?.gB ?? ""}</td>
					<td className="flex-32px score">{scores.get(2)?.gB ?? ""}</td>
					<td className="flex-32px score">{scores.get(3)?.gB ?? ""}</td>
					<td className="flex-32px score">{scores.get(4)?.gB ?? ""}</td>
					<td className="flex-32px score">{points[1]}</td>
				</tr>
			</tbody>
		</table>
	</>);
};
