import { List } from "immutable";
import { create } from "zustand";

export enum Reason {
	None,
	BigShot,
	UnforcedError,
	BigServe,
	DoubleFault,
}

export enum Event {
	None,
	BigShotA,
	BigShotB,
	UnforcedErrorA,
	UnforcedErrorB,
	BigServe,
	Fault,
}

export type Point = {
	readonly pA: number,
	readonly pB: number,
	readonly rA: Reason,
	readonly rB: Reason,
	readonly fault: boolean,
}

export const Point = (pA: number, pB: number, rA: number, rB: number, fault: boolean): Point => Object.freeze({
	pA,
	pB,
	rA,
	rB,
	fault,
});

export type Game = {
	readonly gA: number,
	readonly gB: number,
	readonly serv: boolean,
}

export const Game = (gA: number, gB: number, serv: boolean): Game => Object.freeze({
	gA,
	gB,
	serv,
});

export type Set = {
	readonly gA: number,
	readonly gB: number,
};

export const Set = (gA: number, gB: number): Set => Object.freeze({
	gA,
	gB,
});

export type Maxset = 1 | 2 | 3;
export type Stb = 0 | 12 | 15;
export type Dp = 0 | 1 | 2 | 3 | 4 | 5;

export type AppState = {
	readonly points: List<Point>,
	readonly games: List<Game>,
	readonly sets: List<Set>,
	readonly sA: number,
	readonly sB: number,

	readonly finished: boolean,
	readonly fault: boolean,
	readonly tiebreak: boolean,
	readonly supertiebreak: boolean,

	readonly nameA: string,
	readonly nameB: string,
	readonly maxset: Maxset,
	readonly stb: Stb,
	readonly dp: Dp,
};

export type AppEvents = {
	readonly new: (nameA: string, nameB: string, maxset: Maxset, stb: Stb, dp: Dp) => void,
	readonly event: (event: Event) => void,
	readonly back: () => void,
	readonly serv: () => boolean,
};

export const LOCAL_STORAGE_STATE = "handy-state";

export const DEFAULT_SERV = false;
export const DEFAULT_NAME_A = "Zawodnik A";
export const DEFAULT_NAME_B = "Zawodnik B";
export const DEFAULT_MAXSET: Maxset = 2;
export const DEFAULT_STB: Stb = 0;
export const DEFAULT_DP: Dp = 3;

export const useStore = create<AppState & AppEvents>((set, get) => Object.freeze({
	...restoreState(),

	new: (nameA: string, nameB: string, maxset: Maxset, stb: Stb, dp: Dp) => {
		set({
			points: List.of(Point(0, 0, Reason.None, Reason.None, false)),
			games: List.of(Game(0, 0, DEFAULT_SERV)),
			sets: List<Set>(),
			sA: 0,
			sB: 0,

			finished: false,
			fault: false,
			tiebreak: false,
			supertiebreak: false,

			nameA,
			nameB,
			maxset,
			stb,
			dp,
		}),
		saveState(get());
	},

	event: (event) => {
		let { points, games, sets, sA, sB, finished, fault, tiebreak, supertiebreak } = get();
		const { maxset, stb, dp, serv } = get();

		if (finished) return;

		let lastPoint = points.last()!;
		switch (event) {
			case Event.BigShotA:
				points = points.push(Point(lastPoint.pA + 1, lastPoint.pB, Reason.BigShot, Reason.None, fault));
				fault = false;
				break;
			case Event.BigShotB:
				points = points.push(Point(lastPoint.pA, lastPoint.pB + 1, Reason.None, Reason.BigShot, fault));
				fault = false;
				break;
			case Event.UnforcedErrorA:
				points = points.push(Point(lastPoint.pA, lastPoint.pB + 1, Reason.None, Reason.UnforcedError, fault));
				fault = false;
				break;
			case Event.UnforcedErrorB:
				points = points.push(Point(lastPoint.pA + 1, lastPoint.pB, Reason.UnforcedError, Reason.None, fault));
				fault = false;
				break;
			case Event.BigServe:
				if (!serv()) {
					points = points.push(Point(lastPoint.pA + 1, lastPoint.pB, Reason.BigServe, Reason.None, fault));
				} else {
					points = points.push(Point(lastPoint.pA, lastPoint.pB + 1, Reason.None, Reason.BigServe, fault));
				}
				fault = false;
				break;
			case Event.Fault:
				if (!fault) {
					fault = true;
				} else {
					if (!serv()) {
						points = points.push(Point(lastPoint.pA, lastPoint.pB + 1, Reason.None, Reason.DoubleFault, fault));
					} else {
						points = points.push(Point(lastPoint.pA + 1, lastPoint.pB, Reason.DoubleFault, Reason.None, fault));
					}
					fault = false;
				}
				break;
		}

		lastPoint = points.last();
		let lastGame = games.last()!;

		if (tiebreak) {
			if (lastPoint.pA >= 7 && lastPoint.pA - lastPoint.pB >= 2) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA + 1, lastGame.gB, !lastGame.serv));
			} else if (lastPoint.pB >= 7 && lastPoint.pB - lastPoint.pA >= 2) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA, lastGame.gB + 1, !lastGame.serv));
			}
		} else if (supertiebreak) {
			if (lastPoint.pA >= stb && lastPoint.pA - lastPoint.pB >= 2) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA + 1, lastGame.gB, !lastGame.serv));
			} else if (lastPoint.pB >= stb && lastPoint.pB - lastPoint.pA >= 2) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA, lastGame.gB + 1, !lastGame.serv));
			}
		} else {
			if (lastPoint.pA >= 4 && (lastPoint.pA - lastPoint.pB >= 2 || dp > 0 && lastPoint.pA >= dp + 3)) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA + 1, lastGame.gB, !lastGame.serv));
				tiebreak = checkForTiebreak(games.last(), sets, maxset);
			} else if (lastPoint.pB >= 4 && (lastPoint.pB - lastPoint.pA >= 2 || dp > 0 && lastPoint.pB >= dp + 3)) {
				points = points.push(Point(0, 0, Reason.None, Reason.None, false));
				games = games.push(Game(lastGame.gA, lastGame.gB + 1, !lastGame.serv));
				tiebreak = checkForTiebreak(games.last(), sets, maxset);
			}
		}

		lastPoint = points.last();
		lastGame = games.last();
		if (lastGame.gA >= 6 && lastGame.gA - lastGame.gB >= 2 || lastGame.gA >= 7 && tiebreak || lastGame.gA >= 1 && supertiebreak) {
			games = games.push(Game(0, 0, lastGame.serv));
			sets = sets.push(Set(lastGame.gA, lastGame.gB));
			sA += 1;
			tiebreak = false;
			supertiebreak = checkForSupertiebreak(sets, maxset, stb);
		} else if (lastGame.gB >= 6 && lastGame.gB - lastGame.gA >= 2 || lastGame.gB >= 7 && tiebreak || lastGame.gB >= 1 && supertiebreak) {
			games = games.push(Game(0, 0, lastGame.serv));
			sets = sets.push(Set(lastGame.gA, lastGame.gB));
			sB += 1;
			tiebreak = false;
			supertiebreak = checkForSupertiebreak(sets, maxset, stb);
		}

		if (sA === maxset || sB === maxset) {
			supertiebreak = false;
			finished = true;
		}

		set({ points, games, sets, sA, sB, finished, fault, tiebreak, supertiebreak });
		saveState(get());
	},

	back() {
		let { points, games, sets, sA, sB, finished, fault, tiebreak, supertiebreak } = get();
		const { maxset, stb } = get();

		if (fault) {
			fault = false;
		} else if (points.size > 1) {
			const lastPoint = points.last()!;
			if (lastPoint.pA === 0 && lastPoint.pB === 0) {
				points = points.pop();
				fault = points.last()!.fault;
				points = points.pop();
				const lastGame = games.last()!;
				if (lastGame.gA === 0 && lastGame.gB === 0) {
					games = games.pop();
					games = games.pop();
					const lastSet = sets.last()!;
					if (lastSet.gA > lastSet.gB) {
						sA -= 1;
					} else {
						sB -= 1;
					}
					sets = sets.pop();
					supertiebreak = false;
					tiebreak = checkForTiebreak(games.last(), sets, maxset);
				} else {
					games = games.pop();
					tiebreak = false;
				}
			} else {
				fault = points.last()!.fault;
				points = points.pop();
			}
		}

		if (finished) {
			checkForSupertiebreak(sets, maxset, stb);
			finished = false;
		}

		set({ points, games, sets, sA, sB, finished, fault, tiebreak, supertiebreak });
		saveState(get());
	},

	serv: () => {
		const { points, games, tiebreak, supertiebreak } = get();
		if (!tiebreak && !supertiebreak) {
			return games.last()!.serv;
		} else {
			const lastPoint = points.last()!;
			const serv = (((lastPoint.pA + lastPoint.pB + 1) >>> 1) & 1) !== 0;
			return serv !== games.last()!.serv;
		}
	},
}));

function checkForTiebreak(lastGame: Game, sets: List<Set>, maxset: number): boolean {
	return (maxset === 1 || sets.size < 2 * (maxset - 1)) && lastGame.gA === 6 && lastGame.gB === 6;
}

function checkForSupertiebreak(sets: List<Set>, maxset: number, stb: number): boolean {
	return maxset !== 1 && stb > 0 && sets.size === 2 * (maxset - 1);
}

function restoreState(): AppState {
	const savedState = localStorage?.getItem(LOCAL_STORAGE_STATE);

	if (savedState) {
		const { points, games, sets, ...rest } = JSON.parse(savedState);
		return Object.freeze({
			points: List(points),
			games: List(games),
			sets: List(sets),
			...rest,
		});
	} else {
		return Object.freeze({
			points: List.of(Point(0, 0, Reason.None, Reason.None, false)),
			games: List.of(Game(0, 0, DEFAULT_SERV)),
			sets: List<Set>(),

			sA: 0,
			sB: 0,

			finished: false,
			fault: false,
			tiebreak: false,
			supertiebreak: false,

			nameA: DEFAULT_NAME_A,
			nameB: DEFAULT_NAME_B,

			maxset: DEFAULT_MAXSET,
			stb: DEFAULT_STB,
			dp: DEFAULT_DP,
		});
	}
}

function saveState({ points, games, sets, ...rest }: AppState) {
	const json = {
		points: points.toArray(),
		games: games.toArray(),
		sets: sets.toArray(),
		...rest,
	};
	const jsonString = JSON.stringify(json);
	try {
		localStorage?.setItem(LOCAL_STORAGE_STATE, jsonString);
	} catch {}
}
